/*------------------------------------*\
    商品特色區塊 */
/*------------------------------------*\ */

.feature {

    // 經典系列商品
    &-classic {

        &-list {
            display: flex;
            margin-top: 48px; }

        &-item {
            margin-right: 24px;
            &:last-child {
                margin-right: 0; } }

        &-img {
            margin-bottom: 8px; } }

    // 聯名設計商品
    &-signed {
        @include backgroundCover('../images/bg.png');

        &-list {
            display: flex;
            margin-top: 48px;
            @include size(100%,auto); }

        &-item {
            @include size(100%);
            cursor: pointer;
            >h4 {
                transition: .3s; }

            &:hover {
                >h4 {
                    background-color: $color-primary;
                    color: #fff; } } }

        &-img {
            @include size(100%, 240px); } } }

// 商品圖片
.woman-img {
    @include backgroundCover('../images/product-8.png'); }

.glasses-img {
    @include backgroundCover('../images/product-9.png'); }
