/*------------------------------------*\
    顧客推薦設定 */
/*------------------------------------*\ */

.recommend {

    &-list {
        @include flexGrid(space-between);
        margin-top: 48px;

        &-item {
            @include size(25%, 511px);
            border-radius: 4px;
            overflow: hidden;
            box-shadow: 0 2px 6px #00000029;
            margin-right: 24px; } }

    &-img {
        @include size(100%, 50%); }

    &-content {
        padding: 16px;
        height: 50%;
        box-sizing: border-box;
        @include flexGrid(space-between,flex-start,column);
        @include fontSize(16px,24px); }

    &-text {
        display: block; }
    &-date {
        color: $color-darkGary; } }

/* ------------------- 圖片設定 ------------------- */

@for $i from 1 through 4 {
    .recommend-img {
        &.customer-0#{$i} {
            @include backgroundCover('../images/rec-#{$i}.png'); } } }
