@charset "UTF-8";
/*------------------------------------*\
    SASS 檔案整理 */
/*------------------------------------*\ */
/*------------------------------------*\
    mixin 設定 */
/*------------------------------------*\ */
/* ------------------- 斷點設定 ------------------- */
/* ------------------- mixin 設定 ------------------- */
.border-none, .form-submit-btn, .header-btn {
  border: none;
  outline: none; }

/*------------------------------------*\
    CSS reset */
/*------------------------------------*\ */
/* http://meyerweb.com/eric/tools/css/reset/
 * v2.0 | 20110126
 * License: none (public domain) */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
html, body {
  box-sizing: border-box; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

img {
  vertical-align: middle; }

/*------------------------------------*\
    共用樣式設定 */
/*------------------------------------*\ */
/* ------------------- 顏色設定 ------------------- */
.container {
  padding: 0 312px;
  max-width: 1296px; }

.img-hidden {
  overflow: hidden;
  object-position: 50% 50%; }

/* ------------------- 動畫設定 ------------------- */
.link-border-animation, .navbar-menu-link, .footer-menu-link, .footer-copyright-link {
  position: relative; }
  .link-border-animation::after, .navbar-menu-link::after, .footer-menu-link::after, .footer-copyright-link::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 1px;
    background-color: white;
    transition: .3s; }
  .link-border-animation:hover::after, .navbar-menu-link:hover::after, .footer-menu-link:hover::after, .footer-copyright-link:hover::after {
    width: 100%; }

.hover-translate-animation {
  transition: .3s; }
  .hover-translate-animation:hover {
    transform: translateY(-10px); }

.hover-btn-animation {
  transition: .3s; }
  .hover-btn-animation:hover {
    background-color: #650300; }

/*------------------------------------*\
    導覽列設定 */
/*------------------------------------*\ */
.navbar {
  background-color: #AA0601; }
  .navbar-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  .navbar-menu {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center; }
    .navbar-menu-item {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-right: 64px; }
      .navbar-menu-item:last-child {
        margin-right: 0; }
    .navbar-menu-link {
      color: white;
      transition: .2s;
      display: inline-block;
      padding: 8px 0; }
  .navbar-img {
    padding: 16px; }

/*------------------------------------*\
    footer 設定 */
/*------------------------------------*\ */
.footer {
  padding: 48px 0 32px 0;
  background-color: #AA0601; }
  .footer-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  .footer-menu {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    display: flex; }
    .footer-menu-link {
      color: white;
      margin-right: 24px;
      position: relative; }
  .footer-copyright {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
    color: white; }
    .footer-copyright-menu {
      display: flex; }
    .footer-copyright-item {
      margin-right: 48px; }
      .footer-copyright-item:last-child {
        margin-right: 0; }
    .footer-copyright-link {
      color: white;
      position: relative; }

.social-menu {
  display: flex; }
  .social-menu-item {
    margin-right: 24px; }
    .social-menu-item:last-child {
      margin-right: 0; }

.contact-list {
  padding: 35px 0;
  border-bottom: 1px solid white; }
  .contact-list-item {
    display: flex;
    align-items: center; }
  .contact-list-img {
    margin-right: 20px;
    width: 36px;
    height: 36px; }
  .contact-list-link {
    font-size: 36px;
    line-height: 54px;
    font-weight: 400;
    color: white;
    transition: .3s; }
    .contact-list-link:hover {
