    .section-index-title.white {
      color: white; }
  .section-index.fill {
    background-color: #FBF2F2; }
  .section-index-subtitle {
    font-size: 48px;
    line-height: 72px;
    font-weight: bold;
    color: #AA0601;
    background-color: white;
    text-align: center; }
  .section-index-word {
