/*------------------------------------*\
    聯絡我們表單樣式 */
/*------------------------------------*\ */
.form {
  width: 636px;
  margin: 24px 0 48px 0; }
  .form-text {
    display: block;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    margin-bottom: 8px; }
  .form-input {
    width: 100%;
    padding: 6px 12px;
    margin-bottom: 16px;
    background-color: transparent;
    outline: none;
    border: 1px solid black; }
    .form-input:focus {
      outline: none; }
  .form-submit-btn {
    padding: 16px 176px;
    background-color: #AA0601;
    color: white;
    cursor: pointer; }

/*------------------------------------*\
    首頁 banner 區塊 */
/*------------------------------------*\ */
.header {
