/*------------------------------------*\
    首頁公司特色 */
/*------------------------------------*\ */

.company-info {

    &-list {
        @include flexGrid(center,flex-start);
        margin-top: 32px; }

    &-item {
        width: 25%;
        margin-right: 24px;
        @include flexGrid(center,center,column);
        &:last-child {
            margin-right: 0; } }

    // 特色內容
    &-img {
        @include size(83px);
        margin-bottom: 24px; }

    &-title {
        @include fontSize(24px,36px,bold);
        color: $color-third;
        margin-bottom: 8px; }

    &-word {
        @include fontSize(16px,24px); } }
