/*------------------------------------*\
    導覽列設定 */
/*------------------------------------*\ */

.navbar {
    background-color: $color-primary;

    // 導覽列整體架構
    &-item {
        @include flexGrid(space-between); }

    // 導覽列表
    &-menu {
        @include flexGrid;

        &-item {
            @include flexGrid(space-between);
            margin-right: 64px;
            &:last-child {
                margin-right: 0; } }

        &-link {
            color: $color-white;
            transition: .2s;
            display: inline-block;
            padding: 8px 0;
            @extend .link-border-animation; } }

    // LOGO 圖片
    &-img {
        padding: 16px; } }
