/*------------------------------------*\
    首頁 banner 區塊 */
/*------------------------------------*\ */

.header {
    @include size(100%, 787px);
    @include backgroundCover('../images/banner.png');
    @include flexGrid(flex-end);
    text-align: right;

    &-subtitle {
        @include fontSize(36px, 54px);
        color: $color-third; }

    &-title {
        @include fontSize(64px, 96px, bold);
        color: $color-third; }

    &-btn {
        @include fontSize(20px, 30px);
        background-color: $color-primary;
        color: $color-white;
        padding: 6px 12px;
        cursor: pointer;
        @extend .border-none; } }
